@import url('https://fonts.googleapis.com/css2?family=Mate&family=Mina&display=swap');

@font-face{
    font-family: eventName;
    src:url(../font/OpenSansHebrewCondensed-ExtraBold.ttf)
}
*{
    padding: 0;
    margin: 0;
}

.eventDiv{
    width: 300px;
}

.eventImage img{
    border-top-left-radius:10px ;
    border-top-right-radius:10px ;
}

.eventDetails{
    background: #00000082;
    backdrop-filter: blur(5px);
    width: 300px;
    height: 100px;
    border-bottom-left-radius:10px ;
    border-bottom-right-radius:10px ;

}
.eventName{
    font-family: eventName;
font-size: 24px;
font-weight: 100;
text-align: left;
padding-left: 5px;
background: linear-gradient(180deg, #F0D3FF 0%, #AAAEF2 57.67%);
background-clip: text;
-webkit-background-clip: text; 
color: transparent;
text-shadow: 9px 4px 10px  rgba(0,0,0,0.4);
}

